import {HeadFC, PageProps} from "gatsby"
import {Fragment, useContext, useEffect, useRef, useState} from "react"
import Blank from "~/components/Sections/Blank/Blank"
import HeroSection from "~/components/Sections/HeroSection/HeroSection"
import Search from "~/components/Sections/Search/Search"
import Post from "~/components/Sections/Shelf/Post"
import Shelf, {ShelfSortCategories} from "~/components/Sections/Shelf/Shelf"
import Button from "~/components/UI/Button/Button"
import Gap from "~/components/UI/Gap/Gap"
import GenericLink from "~/components/UI/GenericLink/GenericLink"
import InputText from "~/components/UI/InputText/InputText"
import MultiStyleText from "~/components/UI/MultiStyleText/MultiStyleText"
import RichTextField from "~/components/UI/RichText/RichText"
import ScrollButton from "~/components/UI/ScrollButton/ScrollButton"
import SectionContainer from "~/components/UI/SectionContainer/SectionContainer"
import Switch from "~/components/UI/Switch/Switch"
import AuthContext from "~/contexts/AuthContext"
import {useAnalysisPostsQuery} from "~/queries/AnalysisPostsQuery"
import {useAuthenticationLabelsQuery} from "~/queries/AuthenticationLabelsQuery"
import {useLabelsQuery} from "~/queries/LabelsQuery"
import {useLinksQuery} from "~/queries/LinksQuery"
import {useOnDemandPostsQuery} from "~/queries/OnDemandPostsQuery"
import {usePodcastPostsQuery} from "~/queries/PodcastPostsQuery"
import {useVideoPostsQuery} from "~/queries/VideoPostsQuery"
import {PostItem, PostTypes} from "~/types/PostItem"
import {getQueryParam, setQueryParams} from "~/utilities/queryParams"
import {SEO} from "../components/UI/SEO/SEO"
import {PageContext} from "../types/PageContext"

type PrivateAreaPageContext = PageContext<
    Queries.PrivateAreaPageQuery["allContentfulPaginaAreaRiservata"]["edges"][0]["node"]
>

interface PrivateAreaPageProps extends PageProps<any, PrivateAreaPageContext> {}

export const PrivateAreaSectionParam = "section"
export const PrivateAreaSearchParam = "search"
export const PrivateAreaSortParam = "sort"

export enum PrivateAreaSectionIndex {
    index = 0,
    analysis = 1,
    video = 2,
    onDemand = 3,
    podcast = 4,
}

const PrivateAreaPage: React.FC<PrivateAreaPageProps> = props => {
    const privateAreaPageData = props.pageContext.data
    const links = useLinksQuery()

    const getFilteredItems = () => {
        const posts: Array<PostItem> = []
        allPosts.forEach(post => {
            if (selectedSectionName !== PostTypes.all && post.type !== selectedSectionName) return
            if (inputTextValue !== "") {
                let foundInTitle = false
                let foundInDescription = false
                let foundInCategory = false
                let foundInCustomer = false
                let foundInProject = false
                let foundInTags = false

                const lowCaseValueToFind = inputTextValue.toLowerCase()
                if (post.title && post.title.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInTitle = true
                if (post.description_short && post.description_short.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInDescription = true
                if (post.category && post.category.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInCategory = true
                if (post.customer && post.customer.toLowerCase().indexOf(lowCaseValueToFind) > -1)
                    foundInCustomer = true
                if (post.project && post.project.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInProject = true
                if (post.tags) {
                    post.tags.forEach(tag => {
                        if (tag && tag.toLowerCase().indexOf(lowCaseValueToFind) > -1) foundInTags = true
                    })
                }

                if (
                    !foundInTitle &&
                    !foundInCategory &&
                    !foundInDescription &&
                    !foundInTags &&
                    !foundInCustomer &&
                    !foundInProject
                )
                    return
            }

            posts.push(post)
        })
        return posts
    }

    const asyncFIlterItems = async () => {
        return getFilteredItems()
    }

    const allPosts: Array<PostItem> = [
        ...useAnalysisPostsQuery(),
        ...usePodcastPostsQuery(),
        ...useOnDemandPostsQuery(),
        ...useVideoPostsQuery(),
    ]

    const labels = {...useLabelsQuery(), ...useAuthenticationLabelsQuery()}
    const sectionParam = getQueryParam(PrivateAreaSectionParam)
    const searchParam = getQueryParam(PrivateAreaSearchParam)
    const sortParam = getQueryParam(PrivateAreaSortParam)

    const [selectedSectionIndex, setSelectedSectionIndex] = useState<PrivateAreaSectionIndex | number>(
        sectionNameToIndex(sectionParam),
    )

    const [selectedSectionName, setSelectedSectionName] = useState<PostTypes | string>(sectionParam ? sectionParam : "")

    const [selectedSort, setSelectedSort] = useState<ShelfSortCategories | string>(sortParam ? sortParam : "")
    const [inputTextValue, setInputTextValue] = useState(searchParam ? searchParam : "")
    const [shelfItems, setShelfItems] = useState<Array<PostItem>>(getFilteredItems())
    const [isLoading, setLoading] = useState<boolean>(false)
    const authContext = useContext(AuthContext)
    const searchRef = useRef<HTMLElement | null>(null)

    useEffect(() => {
        if (!sectionParam) return
        setSelectedSectionName(sectionParam)
        setSelectedSectionIndex(sectionNameToIndex(sectionParam))
    }, [sectionParam])

    useEffect(() => {
        updateShelf()
    }, [selectedSectionName, selectedSort])

    const onSwitchClickHandler = (event: React.MouseEvent<HTMLButtonElement>) => {
        const selectedType = Number(event.currentTarget.value)
        setSelectedSectionIndex(selectedType)
        setSelectedSectionName(privateAreaSectionsIndexToName(selectedType))
    }

    const onInputChangeValue = (event: React.ChangeEvent<HTMLInputElement>) => {
        setInputTextValue(event.currentTarget.value)
    }

    const onSortChange = (value: ShelfSortCategories | string) => {
        setSelectedSort(value)
    }

    const updateShelf = () => {
        updateUrl()
        setLoading(true)
        asyncFIlterItems()
            .then(posts => {
                setShelfItems(posts)
            })
            .then(() => {
                setLoading(false)
            })
    }

    const onFormSubmitHandler = (event: React.FormEvent<HTMLFormElement>) => {
        event.preventDefault()
        updateShelf()
    }

    const updateUrl = () => {
        setQueryParams(
            {name: PrivateAreaSectionParam, value: selectedSectionName},
            {name: PrivateAreaSearchParam, value: inputTextValue},
            {name: PrivateAreaSortParam, value: selectedSort},
        )
    }

    return (
        <Fragment>
            <HeroSection
                image={privateAreaPageData?.header?.gatsbyImageData}
                imageAlt={privateAreaPageData?.header?.title}
                mobileImage={privateAreaPageData?.headerMobile?.gatsbyImageData}
            >
                <MultiStyleText flex className1="hero-title-part-1-d2bh" className2="hero-title-part-2-d2bh">
                    {privateAreaPageData?.titolo}
                </MultiStyleText>
            </HeroSection>
            {authContext.status === "SIGNED_IN" && (
                <Fragment>
                    <SectionContainer
                        tabbed
                        marginPost
                        customTab={<ScrollButton />}
                        ref={searchRef}
                        className="search-background-k5ec"
                    >
                        <Search title={privateAreaPageData?.titolo}>
                            <Gap centered style={{marginTop: "var(--gutter-4)"}}>
                                <Switch
                                    key={labels.all}
                                    selected={selectedSectionIndex === PrivateAreaSectionIndex.index}
                                    onSelect={onSwitchClickHandler}
                                    value={PrivateAreaSectionIndex.index}
                                >
                                    {labels.all}
                                </Switch>
                                {privateAreaPageData?.filtri?.map((section, index) => {
                                    return (
                                        <Switch
                                            key={section?.slug}
                                            selected={selectedSectionIndex === index + 1}
                                            onSelect={onSwitchClickHandler}
                                            value={index + 1}
                                        >
                                            {section?.titolo}
                                        </Switch>
                                    )
                                })}
                            </Gap>
                            <form onSubmit={onFormSubmitHandler}>
                                <Gap centered>
                                    <InputText
                                        {...(labels.searchInputPlaceholder
                                            ? {placeholder: labels.searchInputPlaceholder}
                                            : {})}
                                        onChange={onInputChangeValue}
                                        value={inputTextValue}
                                        style={{
                                            width: "min(30vw,500px)",
                                            minWidth: "240px",
                                            fontStyle: "italic",
                                        }}
                                    />
                                    <Button color="green" type="submit">
                                        {labels.search}
                                    </Button>
                                </Gap>
                            </form>
                        </Search>
                    </SectionContainer>
                    <SectionContainer>
                        <Shelf
                            withShowMore
                            withSorting
                            loading={isLoading}
                            sortyBy={sortParam}
                            onSortSelect={onSortChange}
                        >
                            {shelfItems.map((item, index) => {
                                return (
                                    <Post
                                        key={index}
                                        date={item.date}
                                        title={item.title}
                                        image={item.image}
                                        category={item.category}
                                        tags={item.tags}
                                        description_short={item.description_short}
                                        slug={item.slug}
                                        type={item.type}
                                        customer={item.customer}
                                        project={item.project}
                                        pdf={item.pdf}
                                    />
                                )
                            })}
                        </Shelf>
                    </SectionContainer>
                </Fragment>
            )}
            {authContext.status !== "SIGNED_IN" && (
                <Fragment>
                    <SectionContainer tabbed marginPost customTab={<ScrollButton />}>
                        <Blank>
                            <RichTextField>{labels.accountRequired?.raw}</RichTextField>
                        </Blank>
                        <div className="switch-container-8hxd">
                            <span className="switch-label-8hxd">
                                {`${labels.missingAccount} `}
                                <GenericLink
                                    onClick={() => authContext.setStatus("SIGN_UP")}
                                    to={links.authentication.url}
                                >
                                    {labels.clickHereLinkLabel}
                                </GenericLink>
                            </span>
                        </div>
                    </SectionContainer>
                </Fragment>
            )}
        </Fragment>
    )
}
export default PrivateAreaPage

