export const sectionNameToIndex = (value: string | undefined | null): number => {
    switch (value) {
        case PostTypes.analysis:
            return PrivateAreaSectionIndex.analysis
        case PostTypes.onDemand:
            return PrivateAreaSectionIndex.onDemand
        case PostTypes.video:
            return PrivateAreaSectionIndex.video
        case PostTypes.podcast:
            return PrivateAreaSectionIndex.podcast
        default:
            return PrivateAreaSectionIndex.index
    }
}
export const privateAreaSectionsIndexToName = (value: number | undefined | null): string => {
    switch (value) {
        case PrivateAreaSectionIndex.analysis:
            return PostTypes.analysis
        case PrivateAreaSectionIndex.onDemand:
            return PostTypes.onDemand
        case PrivateAreaSectionIndex.video:
            return PostTypes.video
        case PrivateAreaSectionIndex.podcast:
            return PostTypes.podcast
        default:
            return PostTypes.all
    }
}

export const Head: HeadFC<any, PrivateAreaPageContext> = props => {
    const privateAreaPageData = props.pageContext.data

    const links = useLinksQuery()

    return (
        <SEO
            title={privateAreaPageData.titolo}
            language={props.pageContext.language}
            image={privateAreaPageData.header?.url}
            imageAlt={privateAreaPageData.header?.title}
            siteUrl={`${process.env.SITE_URL}${links.privateArea.url}`}
        />
    )
}
