import {graphql, useStaticQuery} from "gatsby"
import {useContext} from "react"
import I18nContext from "~/contexts/I18nContext"

export const usePodcastPostsQuery = () => {
    const i18n = useContext(I18nContext)

    const data: Queries.PodcastPostsQuery = useStaticQuery(graphql`
        query PodcastPosts {
            allContentfulContenutoAreaRiservata(filter: {nascosto: {ne: true}, type: {eq: "podcast"}}) {
                nodes {
                    description_short
                    id
                    type
                    title
                    slug
                    node_locale
                    image {
                        gatsbyImageData
                        title
                    }
                    pdf {
                        url
                        title
                    }
                }
            }
        }
    `)

    const localizedData = []
    for (const post of data.allContentfulContenutoAreaRiservata.nodes) {
        if (post.node_locale === i18n.language) {
            localizedData.push(post)
        }
    }
    return localizedData
}
